

































































































































































































































































@import '../styles/bootstrap_theme';

.facet:hover {
  cursor: pointer;
}

.map {
  z-index: 0;
}
.suggestion-input-group {
  position: relative;
}
.suggestion-input {
  position: absolute;
  top: 0;
}
.suggestion-list-group {
  position: relative;
  width: 100%;
}
.suggestion-list {
  width: 100%;
  position: absolute;
  top: 0;
  z-index: 100;
}

.map-modal-button {
  position: absolute;
  bottom: 0;
  right: 0;
}

.reset-bounds-button {
  position: absolute;
  bottom: 0;
  left: 0;
}

#modal-map-wrapper {
  padding-right: 1.5rem !important;
  padding-left: 1.5rem !important;
}

.facet-header-item {
  word-break: break-word;
}
