.suggestion-input-group {
  position: relative;
}
.suggestion-input {
  position: absolute;
  top: 0;
}
.suggestion-list-group {
  position: relative;
  width: 100%;
}
.suggestion-list {
  width: 100%;
  position: absolute;
  top: 0;
  z-index: 100;
}
